import React, { useState, useEffect } from "react";
import { Container, Button } from "react-bootstrap";
import Style from './style.css';
import { Route, BrowserRouter as Router, Switch, Link } from 'react-router-dom';
import Home from './Pages/Home.js';
import About from './Pages/About.js';
import Layout from './Components/Layout';
import { NavigationBar } from './Components/NavigationBar';
import Essen from './Pages/Essen';
import SignIn from './Pages/SignIn';
import NewPage from './Pages/NewPage';
import Restaurant from './Pages/Restaurant';
import Meetup from './Pages/Meetup';
import AppBar from './Components/AppBar';
import BottomAppBar from './Components/BottomAppBar';
import Bewertung from './Pages/Bewertung';
import fire from './Config/Fire';
import SignInCom from './Components/SignInCom';
import Login from './Pages/Login';
import Sport from './Pages/Sport';
import ScrollIntoView from "./Components/ScrollIntoView";
import Profil from './Pages/Profil';
import Aktivitäten from './Pages/Aktivitäten';
import Attraktionen from './Pages/Attraktionen';
import MeineBewertungen from './Pages/MeineBewertungen';
import BewertungLesen from './Pages/BewertungLesen';
import BewertungEssen from "./Pages/BewertungEssen";
import BewertungSport from './Pages/BewertungSport';
import BewertungAktivität from "./Pages/BewertungAktivität";

class App extends React.Component {
    constructor() {
      super();
      this.state = ({
        user: null,
      });
      this.authListener = this.authListener.bind(this);
    }
  
    componentDidMount() {
      this.authListener();
    }
  
    authListener() {
      fire.auth().onAuthStateChanged((user) => {
        console.log(user);
        if (user) {
          this.setState({ user });
          localStorage.setItem('user', user.uid);
        } else {
          this.setState({ user: null });
          localStorage.removeItem('user');
        }
      });
    }  
    
  render() {
    return (
      <React.Fragment>
        <Router>
          <ScrollIntoView>
          <NavigationBar />
          <Layout>
          {this.state.user ? (
            <Switch>
              <Route exact path="/" component={Home} />
              <Route path="/about" component={About} />
              <Route path="/login" component={Login} />
              <Route path="/essen" component={Essen} />
              <Route path="/restaurant" component={Restaurant} />
              <Route path="/meetup" component={Meetup} />
              <Route path="/bewertung" component={Bewertung} />
              <Route path="/sport" component={Sport} />
              <Route path="/profil" component={Profil} />
              <Route path="/aktivitäten" component={Aktivitäten} />
              <Route path="/attraktionen" component={Attraktionen} />
              <Route path="/meine_bewertungen" component={MeineBewertungen} />
              <Route path="/bewertung_lesen" component={BewertungLesen} />
              <Route path="/bewertung_essen" component={BewertungEssen} />
              <Route path="/bewertung_sport" component={BewertungSport} />
              <Route path="/bewertung_aktivität" component={BewertungAktivität} />
            </Switch>
          ) : (<Login />)}
          </Layout>
          </ScrollIntoView>
        </Router>
      </React.Fragment>
    );
  }
}

export default App;
