import React, { useState, useEffect, Fragment } from "react";
import fire from "./Fire";
import { v4 as uuidv4 } from "uuid";
import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';
import TextField from '@material-ui/core/TextField';
import FavoriteIcon from '@material-ui/icons/Favorite';
import Grid from '@material-ui/core/Grid';
import ThumbUpIcon from '@material-ui/icons/ThumbUp';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import DropdownItem from "react-bootstrap/esm/DropdownItem";
import { DropdownButton } from "react-bootstrap";
import { Dropdown } from "react-bootstrap";

const useStyles = makeStyles((theme) => ({
    heroContent: {
      backgroundColor: theme.palette.background.paper,
      padding: theme.spacing(10, 2, 8),
    },

    bewertungContent : {
        padding: theme.spacing(5, 0, 0), 
    },
    button: {
        padding: theme.spacing(5, 0, 0), 
    },
    smallButton: {
        padding: theme.spacing(2, 0, 0), 
        flexGrow: 1,
    },
    root: {
      flexGrow: 1,
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: 'center',
      color: theme.palette.text.secondary,
    },

  }));


function SnapshotFirebase() {
  const { currentUser } = fire.auth()
  const currentUserId = currentUser ? currentUser.uid : null;
  var user = fire.auth().currentUser;
  const classes = useStyles();  
  const [bewertungen, setBewertungen] = useState([]);
  const [loading, setLoading] = useState(false);
  const [title, setTitle] = useState("");
  const [text, setText] = useState("");
  const [name, setName] = useState("");
  const [kategorie, setKategorie] = useState("");
 

  const ref = fire.firestore().collection("bewertungen");

  //REALTIME GET FUNCTION
  function getBewertungen() {
    setLoading(true);
    ref
    .where('kategorie', '==', 'Restaurant')
    .onSnapshot((querySnapshot) => {
        const items = [];
        querySnapshot.forEach((doc) => {
            items.push(doc.data());
        });
        setBewertungen(items);
        setLoading(false);
    })
}

  useEffect(() => {
    getBewertungen();
    // eslint-disable-next-line
  }, []);

  // ADD FUNCTION
    function addBewertung() {
        const owner = currentUser ? currentUser.uid : 'unknown';
        const ownerEmail = currentUser ? currentUser.email : 'unknown';
        const newBewertung = {
            title,
            text,
            id: uuidv4(),
            owner,
            ownerEmail,
            kategorie,
        };    
    ref
      //.doc() use if for some reason you want that firestore generates the id
      .doc(newBewertung.id)
      .set(newBewertung)
      .catch((err) => {
        console.error(err);
      });
  }

  //DELETE FUNCTION
  function deleteBewertung(bewertung) {
    ref
      .doc(bewertung.id)
      .delete()
      .catch((err) => {
        console.error(err);
      });
  }

  // EDIT FUNCTION
  function editBewertung(updatedBewertung) {
    setLoading();
    ref
      .doc(updatedBewertung.id)
      .update(updatedBewertung)
      .catch((err) => {
        console.error(err);
      });
  }

  return (
    <Fragment>
    <h1 mb="5">Hier eine neue Bewertung hinzufügen</h1>
    <div className={classes.bewertungContent}>
    <h5>Was möchtest Du bewerten?</h5>
    </div>
      <div className="inputBox">
      <input className="inputBoxText"
          type="text"
          value={kategorie}
          placeholder="Restaurant, Lieferdienst, Fittnesstudio oder Aktivität?"
          onChange={(e) => setKategorie(e.target.value)}
        />
        <input className="inputBoxText"
          type="text"
          value={title}
          placeholder="Hier den Titel Deiner Bewertung eingeben"
          onChange={(e) => setTitle(e.target.value)}
        />
        <textarea className="inputBoxText" placeholder="Hier den Text Deiner Bewertung eingeben" value={text} onChange={(e) => setText(e.target.value)} />
        <div className={classes.button}>
        <Button onClick={() => addBewertung({ title, kategorie, text, id: uuidv4() })}
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary">
          Bewertung hinzufügen
        </Button>
        </div>
      </div>
      <hr />
      
      {bewertungen.map((bewertung) => (
        <div className="bewertung" key={bewertung.id}>
        <div className={classes.bewertungContent}>  
          <h4>{bewertung.title}</h4>
          <h6>{bewertung.name}</h6>
          <div className="inputBoxText">
          <p>{bewertung.text}</p>
          </div>
        </div>  
          <div>    

            <Grid item xs>
          <FavoriteIcon />
            </Grid>

            <Grid container>
            <Grid item>  
            <div className={classes.smallButton}>  
            <Button onClick={() => deleteBewertung(bewertung)}
             variant="contained"
             color="secondary">
                 Löschen
             </Button>
             </div>
             </Grid>
             
             <Grid item>  
             <div className={classes.smallButton}> 
            <Button onClick={() =>
                editBewertung({ title: bewertung.title, text, id: bewertung.id })
              }
             type="submit"
             variant="contained"
             color="primary">
              Bearbeiten
            </Button>
            </div>
            </Grid>
            </Grid>
            </div>
          </div>
      ))}
    </Fragment>
  );
}

export default SnapshotFirebase;