
import React, { useState } from 'react';
import BewertungForm from './BewertungForm';
import { RiCloseCircleLine } from 'react-icons/ri';
import { TiEdit } from 'react-icons/ti';
import View from "react";
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));

const BewertungCom = ({ bewertungen, completeBewertung, removeBewertung, updateBewertung }) => {
  const classes = useStyles();
  const [edit, setEdit] = useState({
    id: null,
    value: ''
  });

  const submitUpdate = value => {
    updateBewertung(edit.id, value);
    setEdit({
      id: null,
      value: ''
    });
  };

  if (edit.id) {
    return <BewertungForm edit={edit} onSubmit={submitUpdate} />;
  }

  return bewertungen.map((bewertung, index) => (
    <div
      className={bewertung.isComplete ? 'bewertung-row complete' : 'bewertung-row'}
      key={index}
    >
      <div key={bewertung.id} onClick={() => completeBewertung(bewertung.id)}>
        {bewertung.text}
      </div>
      <div className={classes.root}>
      <div className='icons'>
      <Grid item xs={6}>
      <Paper className={classes.paper}>
        <Button
          onClick={() => removeBewertung(bewertung.id)}
          className='delete-icon'
        />
        </Paper>
        </Grid>
        <Grid item xs={6}>
        <Paper className={classes.paper}>
        <Button
          onClick={() => setEdit({ id: bewertung.id, value: bewertung.text })}
          className='edit-icon'
        />
        </Paper>
        </Grid>
      </div>
      </div>
    </div>
  ));
};

export default BewertungCom;