import React, { useState, useEffect, Fragment } from "react";
import fire from "./Fire";
import { v4 as uuidv4 } from "uuid";

function GetFirebase() {
  const [bewertungen, setBewertungen] = useState([]);
  const [loading, setLoading] = useState(false);
  const [title, setTitle] = useState("");
  const [text, setText] = useState("");
  const [name, setName] = useState("");
  const [id, setId] = useState("");

  const ref = fire.firestore().collection("Bewertungen");

  //ONE TIME GET FUNCTION
  function getBewertungen() {
    setLoading(true);
    ref.get().then((item) => {
      const items = item.docs.map((doc) => doc.data());
      setBewertungen(items);
      setLoading(false);
    });
  }
  useEffect(() => {
    getBewertungen();
    // eslint-disable-next-line
  }, []);

  // ADD FUNCTION
  function addBewertung(newBewertung) {
    ref
      //.doc() use if for some reason you want that firestore generates the id
      .doc(newBewertung.id)
      .set(newBewertung)
      .then(() => {
        setBewertungen((prev) => [newBewertung, ...prev]);
      })
      .catch((err) => {
        console.error(err);
      });
  }

  //DELETE FUNCTION
  function deleteBewertung(bewertung) {
    ref
      .doc(bewertung.id)
      .delete()
      .then(() => {
        setBewertungen((prev) =>
          prev.filter((element) => element.id !== bewertung.id)
        );
      })
      .catch((err) => {
        console.error(err);
      });
  }

  // EDIT FUNCTION
  function editBewertung(updatedBewertung) {
    setLoading();
    ref
      .doc(updatedBewertung.id)
      .update(updatedBewertung)
      .then(() => {
        setBewertungen((prev) =>
          prev.map((element) => {
            if (element.id !== updatedBewertung.id) {
              return element;
            }
            return updatedBewertung;
          })
        );
      })
      .catch((err) => {
        console.error(err);
      });
  }

  return (
    <Fragment>
      <h1>Bewertungen</h1>
      <div className="inputBox">
        <h3>Add New</h3>
        <input
          type="text"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />
        <input
          type="text"
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
        <textarea value={text} onChange={(e) => setText(e.target.value)} />
        <button onClick={() => addBewertung({ title, text, id })}>
          Bewertung hinzufügen
        </button>
      </div>
      <hr />
      {loading ? <h1>Loading...</h1> : null}
      {bewertungen.map((bewertung) => (
        <div className="bewertung" key={bewertung.id}>
          <h3>{bewertung.title}</h3>
          <p>{bewertung.text}</p>
          <div>
            <button onClick={() => deleteBewertung(bewertung)}>Neue Bewertung</button>
            <button
              onClick={() =>
                editBewertung({ title: bewertung.title, text, id: bewertung.id })
              }
            >
              Edit
            </button>
          </div>
        </div>
      ))}
    </Fragment>
  );
}

export default GetFirebase;
